import Pure from "../Pure";

class SharedMemory {
    /** @type {Pure[]} */
    memory = {};
    register(name = "", element) {
        this.memory[name] = element;
    }
    remove(name = "") {
        delete this.memory[name];
    }
    /** @returns {Pure} */
    get(name = "") {
        let m = this.memory[name];
        if (typeof m === "undefined")
            return null;
        else
            return m;
    }
}

const sharedMemory = new SharedMemory();

export default sharedMemory;