import Log from "../Functions/Log";
import client from "./client";
import eventManager from "./EventManager";

// const HOST = "ws://localhost";
// const HOST = "ws://192.168.1.3";
let SOCKET_ADDRESS = "wss://nalrashidi.herokuapp.com";
if (process.env.NODE_ENV === "development")
    SOCKET_ADDRESS = "ws://192.168.100.122:4096";

{
    let TE = new TextEncoder();
    var Encode = text => TE.encode(text).map((byte, offset) => byte - (offset + 1));
    /** @returns {{}} */
    var Decode = bytes => {
        let stream = "";
        bytes.forEach(byte => { stream += String.fromCharCode(byte) });
        return JSON.parse(stream);
    }
    var Package = (opcode, data) => {
        let content = Encode(JSON.stringify({ uid: client.uid, key: client.key, data }));
        let bytes = new Uint8Array(content.length + 1);
        bytes[0] = opcode;
        bytes.set(content, 1);
        return bytes;
    }
}

let responseTime = 0;

class Socket {
    /** @type {WebSocket} */
    ws = null;
    ready = false;
    pendings = {};
    interval = -1;
    responseTime = 0;
    cache = {};
    // response = [];
    onReady = () => { };
    onPong = () => { };

    constructor() {
        eventManager.create("authorization_updated");
    }

    connect() {
        if (this.ws !== null) {
            console.log("Reset..");
            return this.close();
        }
        let ws = this.ws = new WebSocket(SOCKET_ADDRESS);
        ws.onopen = () => { this.handleOpen(); }
        ws.onmessage = m => { this.handleMessage(m.data); }
        ws.onclose = e => { this.handleClose(e.reason); }
        ws.onerror = e => { this.handleError(e.message); }
    }
    handleOpen() {
        this.ready = true;
        this.onReady();
        clearInterval(this.interval);
        this.interval = setInterval(() => this.ping(), 15000);
        this.ping();
    }
    /** @param {Blob} bytes */
    async handleMessage(bytes) {
        if (bytes === "0") {
            this.responseTime = performance.now() - responseTime;
            this.onPong();
            return;
        }
        if (!(bytes instanceof Blob)) return;
        let data = new Uint8Array(await bytes.arrayBuffer());
        let opcode = data[0];
        let fn = this.pendings[opcode];
        if (typeof fn === "function") {
            let response = Decode(data.slice(1));
            if (response !== null && typeof response.authorized === "boolean") {
                let updated = client.authorized !== response.authorized;
                client.authorized = response.authorized;
                if (updated) eventManager.trigger("authorization_updated");
            }
            this.cache[opcode] = response;
            fn(response);
        }
    }
    handleClose(reason = "") {
        this.ready = false;
        this.ws = null;
        this.retry = 3;
        Log(`Socket is closed: Reason[${reason}]\nReconnect will be attempted in ${this.retry} seconds.`);
        setTimeout(() => this.connect(), this.retry * 1000);
    }
    handleError(err = "") {
        this.ready = false;
        Log(`Socket encountered error: ${err}`);
        this.close();
    }
    close() {
        if (this.ws !== null) {
            this.ready = false;
            this.ws.close(1000, "[Just closing]");
            this.ws = null;
        }
    }
    /** @param {Uint8Array} bytes */
    send(bytes) {
        if (this.ready && this.ws !== null && ArrayBuffer.isView(bytes) && bytes.length <= 4096)
            this.ws.send(bytes);
    }
    /** @param {function({}): void} hasCache * @param {function({}): void} hasUpdated * @returns {Promise<{}>} */
    async cached(opcode = 0, data = {}, hasCache, hasUpdated) {
        if (typeof this.cache[opcode] !== "undefined" && typeof hasCache === "function") {
            console.time(`Cache[${opcode}]`);
            hasCache(this.cache[opcode]);
            console.timeEnd(`Cache[${opcode}]`);
        }
        console.time(`Response[${opcode}]`);
        return this.request(opcode, data).then(response => {
            console.timeEnd(`Response[${opcode}]`);
            hasUpdated(response);
        });
    }
    /**@returns {Promise<{}>} */
    request(opcode = 0, data = []) {
        if (!this.ready || this.ws === null) return;
        // Log(`Request[${opcode}]: ${JSON.stringify(data)}`);
        let bytes = Package(opcode, data);
        if (bytes.length < 4096) {
            this.ws.send(bytes);
            return new Promise(resolve => {
                this.pendings[opcode] = resolve;
            });
        }
    }
    ping() {
        if (!this.ready || this.ws === null) return;
        responseTime = performance.now();
        this.ws.send(0);
    }
}

let socket = new Socket();

export default socket;