import React from "react";
import sharedMemory from "./CLASSES/SharedMemory";

export default class Pure extends React.PureComponent {
    state = {};
    registered = typeof this.props.register !== "undefined";
    componentIsReady = false;
    componentDidMount() {
        this.componentIsReady = true;
        if (this.registered)
            sharedMemory.register(this.props.register, this);
        this.onReady();
    }
    componentWillUnmount() {
        this.componentIsReady = false;
        this.onRemoved();
        if (this.registered)
            sharedMemory.remove(this.props.register);
    }
    onReady() { }
    onRemoved() { }
    updateState(data = {}) {
        if (this.componentIsReady)
            this.setState(data);
    }
}